import React, { lazy, Component } from 'react'
import {
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
import swal from 'sweetalert';
import CIcon from '@coreui/icons-react'

import * as moment from 'moment'
import Axios from '../../lib/api';

const WidgetsDropdown = lazy(() => import('../widgets/WidgetsDropdown.js'))
const WidgetsBrand = lazy(() => import('../widgets/WidgetsBrand.js'))


class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: []
    }
  }
  componentDidMount = () => {
    Axios.get('admin/page/pages').then(r => {
      console.log(r)
      this.setState({ list: r.data })
    })
  }
  onView(id) {
    this.props.history.push(`/admin/view/${id}`);
  }
  onVisit(id) {
    this.props.history.push(`/admin/view/${id}`);
    var host = process.env.REACT_APP_BASE_URL.replace('api/', '');

    window.open(`${host}public-pages/${id}`, '_blank');
  }
  onEdit(id) {
    this.props.history.push(`/admin/edit/${id}`);
  }
  onDelete(item) {
    swal({
      title: "Are you sure?",
      text: "Once deleted, you will not be able to recover this page!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    })
      .then(willDelete => {
        if (willDelete)
          Axios.delete(`admin/page/page/${item.id}`).then(r => {
            console.log(r)
            var { list } = this.state;
            list = list.filter(page => page.id !== item.id);
            this.setState({ list: list })
          })
      })

  }
  onDuplicate(item) {
    try {
      item = JSON.parse(JSON.stringify(item))
      delete item.id
      Axios.post('admin/add-page', item).then(r => {
        console.log(r)
        this.props.history.push(`/admin/edit/${r.data.id}`);
      })
    } catch (e) {
      console.log(e)
    }
  }
  render() {
    var { list } = this.state;
    return (
      <>
        <CRow>
          <CCol>
            <CCard>
              <CCardHeader>
                Page List
              </CCardHeader>
              <CCardBody>

                <CButton type="submit" size="sm" color="warning" onClick={() => { this.props.history.push('/admin/new'); }}><CIcon name="cil-scrubber" /> Add</CButton>

                <br />

                <table className="table table-hover table-outline mb-0 d-none d-sm-table">
                  <thead className="thead-light">
                    <tr>
                      <th>Name</th>
                      <th className="text-right">View</th>
                    </tr>
                  </thead>
                  <tbody>
                    {list.map(page => {
                      var date = moment(new Date(page.createdAt)).format('DD MMMM YYYY')
                      return <tr key={page.id}>

                        <td>
                          <div>{page.title}</div>
                          <div className="small text-muted">
                            <span>New</span> | Registered: {date}
                          </div>
                        </td>

                        <td className="text-right">
                          <button className="small btn btn-success" onClick={() => this.onVisit(page.id)}>Visit</button>
                          <button className="small btn btn-success" onClick={() => this.onView(page.id)}>View</button>
                          <button className="small btn btn-primary" onClick={() => this.onEdit(page.id)}>Edit</button>
                          <button className="small btn btn-danger" onClick={() => this.onDelete(page)}>Delete</button>
                          <button className="small btn btn-info" onClick={() => this.onDuplicate(page)}>Duplicate</button>
                        </td>
                      </tr>

                    })}

                  </tbody>
                </table>

              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </>
    )
  }
}

export default Dashboard
